.app__footer {
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background-color: var(--color-black);
  padding-top: 0;
}

.app__footer-links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  margin-top: 5rem;
  padding: 0 1rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
  flex: 1;
  margin: 1rem;
  text-align: center;
}

.app__footer-links_logo img:first-child {
  width: 15rem;
  margin-bottom: 0.75rem;
}

.app__footer-links_work p:nth-child(2n + 1) {
  margin-bottom: 1rem;
}

.app__footer-links_icons {
  margin-top: 0.5rem;
}


.app__footer-links_icons svg {
  color: var(--color-white);
  margin: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
}

.app__footer-links_icons svg:hover {
  color: var(--color-golden);
}

.app__footer-headtext{
  font-family: var(--font-base);
  color: var(--color-white);
  margin-bottom: 1rem;
  font-weight: 900;
  text-transform: capitalize;
  font-size: 2rem;
}

.footer__copyright{
  margin-top: 3rem;
}

@media screen and (max-width:650px) {
  .app__footer-links{
    align-items: center;
    flex-direction: column;
    gap: 1rem;
  }
  .app__footer{
    padding: 0 0 2rem 0;
  }
  .app__footer-links_logo img:first-child {
    width: 15rem;
    margin-bottom: 0.5rem;
  }
}